export default {
	data() {
		return {
			indexImg: this.$indexImg,
			videoImg: this.$videoImg,
			myActiveImg: this.$myActiveImg,
			downAppImg: this.$downAppImg,
			vipImg: '',

			login: false,
			avatarUrl: '/static/logo.png',
			uerInfo: {},
			userInfo: {},
			token: '',
			vipEndTime: '',
		}
	},
	onLoad() {
		let self = this;
		uni.getStorage({
			key: 'user_info',
			success: function(res) {
				self.userInfo = res.data;
				self.token = res.data.token;
				if (res.data.vip) {
					self.vipImg = self.$vipTrue
				} else {
					self.vipImg = self.$vipFalse
				}
			}
		})
		
		var nowTime = Date.parse(new Date());
		// console.log(nowTime)
		nowTime = Number((this.userInfo.validDate * 60 * 60 * 1000) + nowTime);
		// console.log(nowTime)
		var date = new Date(nowTime);
		var Y = date.getFullYear()
		var M = date.getMonth() + 1;
		var D = date.getDate();
		var h = date.getHours();
		var m = date.getMinutes();
		this.vipEndTime = Y + '-' + M + '-' + D;
	},
	methods: {
		goLogin() {
			if (!this.login) {
				uni.navigateTo({
					url: '/pages/login/login'
				});
			}
		},
		goPay() {
			uni.navigateTo({
				url: '/pages/center/pay'
			});
		},
		goAbout() {
			uni.navigateTo({
				url: '/pages/about/about'
			});
		},
		gotoHome() {
			uni.redirectTo({
				url: '/pages/new/new'
			});
		},
		loginOut() {
			let self = this;
			if (this.token) {
				uni.showModal({
					title: '',
					content: '是否退出当前账号并跳转登录',
					showCancel: true,
					cancelText: '取消',
					cancelColor: '#9C9C9C',
					confirmText: true,
					confirmText: '确定',
					confirmColor: '#E96900',
					success: function(res) {
						if (res.confirm) {
							self.token = '';
							uni.removeStorage({
								key: 'user_info',
								success: function() {
									uni.navigateTo({
										url: '../../pages/login/login'
									})
								}
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})

			} else {
				uni.showToast({
					title: '尚未登录',
					icon: 'none',
				})
			}
		},
		gotoVideo() {
			uni.redirectTo({
				url: '../../pages/video/video'
			})
		}
	}
}
